import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import {Button} from 'antd';


class Login extends Component {

    constructor(props) {
        super(props);
        this.emailRef = React.createRef();
    }

    state = {
        login: true,
        email: '',
        phone: '',
        password: '',
        passwordA: '',
        name: ''
    }

    componentDidMount() {
    }

    // _confirm = async data => {
    //     const {token} = this.state.login ? data.login : data.signup
    //     this._saveUserData(token)
    //     this.props.history.push(`/`)
    // }
    //
    // _saveUserData = token => {
    //     localStorage.setItem(AUTH_TOKEN, token)
    // }
    //
    // _checkEmail = debounce((e, mutation) => {
    //     // if(!validateEmail(email)) {
    //     //     console.log('mutation');
    //     //     // return;
    //     // }
    //     // mutation();
    //     console.log(e, mutation)
    //
    //     this.emailRef.current.setCustomValidity("This field cannot be left blank");
    //     console.log(this.emailRef.current);
    //     mutation();
    //     // {
    //     //     const {
    //     //         email
    //     //     } = this.state;
    //     // }
    // }, 500)

    confirmClicked = (a, b) => {
        alert(`${a}, ${b}`);
    }

    render() {

        const {
            login,
            phone,
            email,
            password,
            passwordA,
            name
        } = this.state;
        return (
            <div>
                <div>
                    <div className="landing">
                        <div className="landing-header">
                            <div className="landing-header__operator">
                                <div className="landing-header__operator">
                                    <img className="landing-header__operator-min"
                                         src="http://www.wulihub.com.cn/go/Qxa7kJ/images/详情1/u21.png"/>
                                </div>
                                <div className="landing-header__operator">
                                    <img className="landing-header__operator-x"
                                         src="http://www.wulihub.com.cn/go/Qxa7kJ/images/详情1/u20.png"/>
                                </div>
                            </div>
                        </div>

                        <div className="landing-logo">
                            <img className="landing-logo_imgT"
                                 src="http://www.wulihub.com.cn/go/Qxa7kJ/images/账号登录/u464.svg"/>
                            <div className="landing-logo_name">
                                <div className="landing-logo_name--project">
                                    PROJECT
                                </div>
                                <div className="landing-logo_name--avatar">
                                    AVATAR
                                </div>
                            </div>
                        </div>

                        <form action="#">
                            <div className="landing-form form">
                                {
                                    !login && (
                                        <div className="form__group">
                                            <input type="text" className="form__input" placeholder="请输入用户名"
                                                   value={name}
                                                   onChange={e => this.setState({name: e.target.value})}
                                                   id="name" required/>
                                            <label htmlFor="name" className="form__label">请输入用户名</label>
                                        </div>
                                    )
                                }


                                <div className="form__group">
                                    <input type="email" className="form__input" placeholder="请输入邮箱"
                                           pattern="/^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/"
                                           value={email}
                                           onChange={e => this.setState({email: e.target.value})}
                                           ref={this.emailRef}
                                        // oninvalid="this.setCustomValidity('Enter User Name Here')"
                                        // oninput="this.setCustomValidity('')"
                                           id="email" required/>
                                    <label htmlFor="email" className="form__label">请输入邮箱</label>
                                </div>

                                {
                                    !login && (
                                        <div className="form__group">
                                            <input type="text" className="form__input" placeholder="请输入手机"
                                                   value={phone}
                                                   onChange={e => this.setState({phone: e.target.value})}
                                                   id="phone" required/>
                                            <label htmlFor="phone" className="form__label">请输入手机</label>
                                        </div>
                                    )
                                }


                                <div className="form__group">
                                    <input type="password" className="form__input" placeholder="请输入密码"
                                           value={password}
                                           onChange={e => this.setState({password: e.target.value})}
                                           id="password" required/>
                                    <label htmlFor="password" className="form__label">请输入密码</label>
                                </div>

                                {
                                    !login && (
                                        <div className="form__group">
                                            <input type="password" className="form__input" placeholder="请再次输入密码"
                                                   value={passwordA}
                                                   onChange={e => this.setState({passwordA: e.target.value})}
                                                   id="passwordA"
                                                   required/>
                                            <label htmlFor="passwordA" className="form__label">请再次输入密码</label>
                                        </div>
                                    )
                                }


                                <div className="form__group">
                                    <div className="form__text-group">
                                        <div className="form__sms-login">
                                            <a>短信验证码登录</a>
                                        </div>
                                        <div className="form__forget-pwd">
                                            <a>忘记密码</a>
                                        </div>
                                    </div>
                                </div>

                                <div className="form__group form__btn-group">
                                    <div className="form__btn-group_item"
                                         onClick={
                                             (e) => console.log(e)
                                         }>
                                        <a className="btn-primary btn-primary--blue">
                                            {login ? '登录' : '注册'}
                                        </a>
                                    </div>
                                    <div className="form__btn-group_item"
                                         onClick={() => this.setState({login: !login})}
                                    >
                                        <a className="btn-primary">
                                            {
                                                login ? '前去注册' : '已有账号'
                                            }
                                        </a>
                                    </div>
                                </div>

                                <div className="dividing"></div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>

        )
    }
}

export default Login
